import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import { BrowserRouter, Route, Switch } from 'react-router-dom'


// import NavBar from './components/NavBar/NavBar'
// import Products from './components/Products/Products'

import { Products,Navbar,Cart,Checkout } from "./components"
import { commerce } from './lib/commerce'

const App = () => {
    const [products, setProducts] = useState([])
    const [cart, setCart] = useState({})
    const [Order, setOrder] = useState({})
    const [ErrorMessage, setErrorMessage] = useState("")

    const product = useSelector(state => state.product)


    const fetchProducts = async () => {
        const {data } = await commerce.products.list() 
        // const {data } = { data : [1]}
        setProducts(data)
    }

    const fetchCart = async () =>{
        const cart = await commerce.cart.retrieve()

        setCart(cart)
    }

    const handleAddToCart = async (productId, quantity) => {
        const item = await commerce.cart.add(productId,quantity)

        setCart(item.cart)
    }

    const handleUpdateCartQty = async (productId,quantity) => {
        const res = await commerce.cart.update(productId, { quantity } )

        setCart(res.cart)
    }

    const handleRemoveCartQty = async (productId) => {
        const res = await commerce.cart.remove(productId )

        setCart(res.cart)
    }

    const handleEmptyCart = async () => {
        const res = await commerce.cart.empty()

        setCart(res.cart)
    }

    const refreshCart = async () => {
        const newCart = await commerce.cart.refresh()

        console.log(newCart)

        setCart(newCart)

    }

    const handleCaptureCheckout = async (checkoutTokenId , newOrder) => {
        try {
            const incomingOrder =  await commerce.checkout.capture(checkoutTokenId,newOrder)


            setOrder(incomingOrder)
            refreshCart()

        } catch (e) {
            setErrorMessage(e.data.error.message)
        }
    }




    useEffect(()=> {
        fetchProducts()
        fetchCart()
    },[])
    
    return (
        <BrowserRouter>
        <div>
            <Navbar totalItems={cart.total_items} />
            <Switch>
                <Route path="/" exact>
                    <Products products={products} onAddToCart={handleAddToCart} />            

                </Route>
                <Route path="/cart" exact>
                    <Cart cart={cart} handleUpdateCartQty={handleUpdateCartQty} handleRemoveCartQty={handleRemoveCartQty} handleEmptyCart={handleEmptyCart} ></Cart>

                </Route>
                <Route path="/checkout" exact>
                    <Checkout cart={cart} order={Order} onCaptureCheckout={handleCaptureCheckout} error={ErrorMessage} />

                </Route>
            </Switch>
        </div>
        </BrowserRouter>

    )
}

export default App
