import { AppBar, Badge, IconButton, Menu, MenuItem, Toolbar, Typography } from '@material-ui/core'
import { ShoppingCart } from '@material-ui/icons'
import React from 'react'
import StoreIcon from '@material-ui/icons/Store';

import useStyles from "./styles"
import { Link, useLocation } from 'react-router-dom';


const NavBar = ({totalItems}) => {
    const classes = useStyles()
    const location = useLocation()
    return (
        <>
            <AppBar position="fixed" className={classes.appBar}>
                <Toolbar>
                    <Typography component={Link} to="/" variant="h6" className={classes.title} color="inherit">
                        <StoreIcon height="25px" className={classes.image} /> 
                        Iezy

                    </Typography>
                    <div className={classes.grow} />

                    {location.pathname !== "/cart" && location.pathname !== "/checkout" && (<div className={classes.button}>
                        <IconButton component={Link} to="/cart" aria-label="Show cart items" color="inherit" >
                            <Badge badgeContent={totalItems} color="secondary">
                                <ShoppingCart/>

                            </Badge>
                        </IconButton>
                    </div>)}


                </Toolbar>
                
            </AppBar>  
        </>
    )
}

export default NavBar
