import { List, ListItem, ListItemText, Typography } from '@material-ui/core'
import React from 'react'

const Review = ({checkoutToken}) => {
    
    return (
        <>
            <Typography variant="h6" gutterButtom>Order Summary</Typography>
            <List disablePadding>
                {checkoutToken.live.line_items.map(p => (
                    <ListItem style={{padding:"10px 0"}} key={p.name}>
                        <ListItemText primary={p.name} secondary={`Quantity : ${p.quantity}`} />
                        <Typography variant="body2"> {p.line_total.formatted_with_symbol} </Typography>
                    </ListItem>
                ) )}

                <ListItem style={{padding:"10px 0"}}>
                    <ListItemText primary="Total" />
                    <Typography variant="subtitle1" style={{fontWeight :700}}> {checkoutToken.live.subtotal.formatted_with_symbol} </Typography>
                </ListItem>
            </List>
        </>
    )
}

export default Review
